import styled from "styled-components";

export default function Main({children}) {
    return (
        <MainWrapper>{children}</MainWrapper>
    )
}
const MainWrapper = styled.div`
    display: block;
    position: relative;
    max-width: 80%;
    width: 100%;
    margin: 0 auto;
    padding: 0;
    @media screen and (max-width: 800px) {
        max-width: 95%;
    }
`;